html {
  box-sizing: border-box;
}

body {
  font-family: "TTLakes", sans-serif;
  margin: 0;
  min-height: 663px;
  background-color: #f8f8f8;
}

.container {
  position: absolute;
  height: 100%;
  min-height: 662px;
  left: 0;
  width: 100%;
  margin: 0;
  padding-left: 16px;
  box-shadow: 25px 0 40px -40px black;
  background-color: #f8f8f8;
}

.swiper-pagination_type {
  bottom: 10px;
}
.main{
  background-color: #f8f8f8;
}

.header,
.footer-Buttons {
  display: flex;
  flex-direction: row;
}

.content__text {
  margin-bottom: 0;
}

.read-more:before {
  content: url("../img/expand.svg");
  margin-right: 6px;
}

.read-more,
.commercial {
  display: flex;
  text-decoration: none;
  padding: 16px 0 6px 16px;
  border: none;
  background-color: #f8f8f8;
  font-size: 16px;
  vertical-align: middle;
  line-height: 24px;
  cursor: pointer;
}

.content__text_hide {
  font-size: 14px;
}

.commercial {
  justify-content: left;
}

.menu__item:hover{
  background-color: lightslategray;
  transform: scale(1.04);
  transition-duration: 0.15s;
  cursor: pointer;
}

.main-header {
  display: flex;
  margin-top: 24px;
  margin-bottom: 0;
  font-size: 28px;
  line-height: 40px;
}

.main-header:before {
  content: url("../img/highlight.svg");
  margin-left: -16px;
  margin-right: 12px;
}

.slider__menu {
  flex-wrap: wrap;
  margin-top: 0;
  padding-left: 0;
}

.slider>ul {
  display: flex;
  flex-direction: row;
  margin-bottom: 0;
  overflow-x:hidden;
  flex-wrap: nowrap;
}

.slider__label > a {
  text-decoration-line: none;
  color: #7E7E82;
  cursor: pointer;
  font-size: 16px;
  height: 40px;
  padding: 10px 16px;
}

.slider__label {
  display: flex;
  color: #7E7E82;
  align-items: center;
  line-height: 24px;
  white-space: nowrap;
  margin-top: 10px;
  margin-bottom: 12px;
  margin-left: 4px;
  height: 40px;
  text-align: center;
}

.slider__label_active {
  margin-left: 0;
  background-color: #FFFFFF;
  border: 2px solid #B8FFEC;
  border-radius: 6px;
}

.slider__menu {
  overflow-x: scroll;
}

.header__button:hover,
.footer-Buttons__button:hover {
  transform: scale(1.04);
  transition-duration: 0.4s;
  opacity: 60%;
  cursor: pointer;
}

.header {
  margin-top: 0;
  background-color: #FFFFFF;
  padding: 16px 0;
}

.header__hide {
  display: none;
}

.header__button,
.footer-Buttons__button,
.footer__lang__button {
  background-color: inherit;
  border: none;
}

.menu__item {
  list-style: none;
  line-height: 24px;
  margin-top: 16px;
  margin-bottom: 16px;
  width: 220px;
}

.menu__item a{
  text-decoration: none;
  color: black;
}

.menu__item_active a {
  color: #7E7E82;
}

.menu__item a:hover{
  color: white;
}

.content__text {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.5px;
  color: #1B1C21;
}

.content__logo {
  margin-left: -16px;
  margin-bottom: 14px;
  width: 320px;
}

.subheader {
  background-image: linear-gradient(to right, #f8f8f8, #fff);
  border-top: 1px solid #B8FFEC;
  margin-left: -14px;
  margin-top: 4px;
  line-height: 24px;
  padding-top: 0;
}

.footer-Buttons__button {
  margin-right: 16px;
  padding: 0;
}

.footer__mail:hover,
.footer__phone-number:hover {
  background-color: #499ce1;
  color: rgba(255, 255, 255, 0.9);
  transform: scale(1.04);
  transition-duration: 0.15s;
  cursor: pointer;
}

.footer__lang__button:hover {
  background-color: #f5f0f0;
  cursor: pointer;
}

.footer__lang__button_active {
  color: #7E7E82;
}

.footer-content {
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}

.footer_main {
  border-top: 1px solid #B8FFEC;
}

.footer__left,
.footer__center,
.footer__right {
  font-weight: normal;
  margin-bottom: 18px;
  color: #7E7E82;
  font-size: 14px;
  text-align: center;
}

.overlay {
  margin: 0;
  top: 0;
  position: absolute;
  z-index: 24;
  height: 200%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
